.login-page {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.login-page .form {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("../../assets/login-heart.png");
  transform: translateY(-5%);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.login-page .form form {
  width: 35%;
  overflow: hidden;
  margin-top: 2rem;
}

.textarea {
  width: 100%;
  position: relative;
  height: 50px;
  margin: 1px auto;
}

.form input {
  width: 100%;
  height: 100%;
  color:white;
  padding-top: 20px;
  border: none;
  outline: none;
  background:none;
}

.form label {
  position: absolute;
  bottom: 0px;
  left: 0%;
  width: 100%;
  height: 100%;
  pointer-events: none;
  border-bottom: 1px solid black;
}

.form label::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -1px;
  width: 100%;
  height: 100%;
  border-bottom: 3px solid #5fa8d3;
  transform: translateX(-101%);
  transition: all ease 300ms;
}

.content-name {
  position: absolute;
  bottom: 5px;
  left: 0px;
  transition: all ease 300ms;
}

.form input:focus + .label-name .content-name,
.form input:valid + .label-name .content-name {
  transform: rotate(-360deg) translateY(-150%);
  font-size: 14px;
  color: #5fa8d3;
}

.form input:focus + .label-name::after,
.form input:valid + .label-name::after {
  transform: translateX(0%);
}

.btn-login {
  margin-top: 10px;
  padding: 3px;
  border-radius: 30px;
  cursor: pointer;
  background-color: #2596d8;
  font-weight: bold;
  color: rgb(195, 187, 187);
  border: none;
  transition: all ease 300ms;
}

.btn-login:hover {
  background-color: #5fa8d3;
  color:black;
}

.fullwidth {
  width: 100%;
}

.remember-me {
  margin-top: 5px;
  display: flex;
  height: 50px;
  align-items: center;
  font-size: 13px;
  text-transform: capitalize;
  color: rgb(165, 156, 156);
}

.remember-me input {
  height: 15px;
  width: auto;
  margin-right: 5px;
}

@media (max-width: 689px) {
  .login-page .form {
    background-size: cover;
  }
  .login-page .form form {
    width: 80%;
    margin-top: 3rem;
  }
}
